<template>
    <div>
        <div class="kd-title-content-wrapper payResult">
            <p>扫码支付：<span>{{ totalMoney }}</span>元</p>
            <p>订单号：{{ outTradeNo }}</p>
            <qriously
                class="payValue"
                :value="qriously"
                :size="260"
            />
            <p>打开支付宝“扫一扫”功能，扫描上方二维码</p>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { checkPayResult } from '@/service/convenient/identificationPhoto';

export default {
    name: 'IdentificationPhotoPay',
    computed: {
        ...mapState('identificationPhoto', {
            photoPrintInfoId: 'photoPrintInfoId',
            outTradeNo: 'outTradeNo',
            totalMoney: 'totalMoney',
            qriously: 'qriously'
        })
    },
    data () {
        return {
            timeOutId: null
        };
    },
    mounted () {
        this.timeOutId = setInterval(() => {
            this.printPhoto();
        }, 2000);
    },
    beforeDestroy () {
        clearInterval(this.timeOutId);
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        async printPhoto () {
            const res = await checkPayResult({
                businessId: this.photoPrintInfoId,
                businessType: 'photo'
            });
            if (res && res.result === 0) { // 未支付
                // this.changeTipPage({message: '您还未支付，请返回并且支付后重试', enableBack: true});
            } else if (res && res.result === 1) { // 支付成功
                this.changeStep('down');
            } else if (res && res.result === 2) { // 支付失败
                // this.changeTipPage({message: '未能支付成功，请返回并且支付后重试', enableBack: true});
            } else if (res && res.result === 3) { // 已经退款
                this.changeTipPage({ message: res.message });
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .payResult {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 26px;

        p {
            margin-bottom: 24px;

            span {
                font-size: 48px;
            }
        }

        .payValue {
            background: #fff;
            margin-bottom: 24px;
        }
    }
</style>
