<template>
    <div>
        <div class="kd-page-title">
            照片采集
        </div>
        <div class="kd-page-content-wrapper collect-complete-box">
            <div class="chose-img">
                <img
                    v-if="previewPrintPhoto"
                    :src="previewPrintPhoto"
                    alt=""
                >
                <img
                    v-else
                    :src="cameraDefault"
                    alt=""
                >
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="重拍"
                :button-handle="goUp"
            />
            <kd-button
                v-if="CONSTANT.needPayPhoto"
                icon="kedaIcon-circleleft"
                text="加印照片"
                :button-handle="overprint"
            />
            <kd-button
                icon="kedaIcon-circleright"
                :text="CONSTANT.needPayPhoto ? '支付' : '照片打印'"
                :button-handle="savePay"
            />
        </div>
        <!--打印照片-->
        <overprint-photo
            :dialog-visible="overprintVisible"
            @sure="sure"
            @cancel="cancel"
        />
    </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex';
import overprintPhoto from './overprint';
import { wsSend } from '@/utils/websocket';
import cameraDefault from '@/assets/images/default/cameraDefault.png';

export default {
    name: 'IdentificationPhotoCollectComplete',
    components: { overprintPhoto },
    data () {
        return {
            overprintVisible: false,
            cameraDefault: cameraDefault
        };
    },
    beforeDestroy () {
        wsSend(this.lightWebSocket, 'lightCtrl@B5:OFF');
    },
    computed: {
        ...mapState('identificationPhoto', {
            previewPrintPhoto: 'previewPrintPhoto',
            photoType: 'photoType',
            printCount: 'printCount'
        }),
        ...mapState('common', {
            payMethod: 'payMethod'
        }),
        ...mapState('permanent', {
            lightWebSocket: 'lightWebSocket'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        // 立即支付
        async savePay () {
            this.changeStep('down');
        },
        // 加印照片
        overprint () {
            this.overprintVisible = true;
        },
        sure () {
            this.overprintVisible = false;
            this.changeStep('paymentWay');
        },
        cancel () {
            this.overprintVisible = false;
        },
        goUp () {
            this.changeStep('photoCollection');
        }
    }
};
</script>

<style lang="scss" scoped>
    .collect-complete-box {
        display: flex;
        justify-content: center;
        align-items: center;

        .chose-img {
            width: 700px;
            height: 500px;
            display: flex;
            justify-content: center;

            img {
                max-width: 100%;
                max-height: 100%;
            }
        }
    }
</style>
