<template>
    <div>
        <div class="kd-page-title">
            请选择您要办理的业务
        </div>
        <div class="wrap-info">
            <el-row>
                <el-col
                    :span="12"
                    v-for="(item, index) in typeList"
                    :key="item.id"
                    class="detail-item"
                    :class="{active:typeActive===index}"
                    @click.native="toggle(index, item)"
                >
          <span
              class="kedaIconfont"
              :class="item.icon"
          />
                    <span>{{ item.name }}</span>
                    <span
                        class="photo-desc"
                        v-if="item.type === 0"
                    >(一寸白底)</span>
                </el-col>
            </el-row>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
    name: 'IdentificationPhotoType',
    data () {
        return {
            typeActive: null			// 选中类型
        };
    },
    computed: {
        ...mapState('identificationPhoto', {
            typeList: 'typeList'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('identificationPhoto', ['setPhotoType']),
        toggle (i, item) {
            this.typeActive = i;
            this.setPhotoType(item);
            if (item.type === 1) {		// 业务为标准1、2寸证件照相 要选择证件类别
                this.changeStep('down');
                return;
            }
            if (item.type === 2) {		// 业务为护照及签证照相 要选择签证类别
                this.changeStep('chooseVisa');
                return;
            }
            this.changeStep('photoCollection');
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        width: 1180px;
        height: 530px;
        margin: 0 auto;

        .detail-item {
            cursor: pointer;
            box-sizing: border-box;
            padding: 0 3vh;
            margin-top: 35px;
            margin-left: 36px;
            width: 43%;
            height: 114px;
            line-height: 114px;
            background: linear-gradient(#70A3D8, #3A84BF);
            border: 1PX solid #EDFFFF;/*no*/
            border-radius: 1vh;

            span {
                color: #ffffff;
                font-size: 3.5vh;
            }

            span:first-child {
                border-right: 0.2vh solid #8FC9FF;
                padding-right: 3vw;
            }

            span:nth-child(2) {
                padding-left: 2vw;
            }

            span.photo-desc {
                padding-left: 10px;
                font-size: 18px;
            }

            &:active {
                background: linear-gradient(#87b8d8, #6598bf);
            }
        }
    }
</style>
