<template>
    <div class="confirm-loading">
        <div class="loading-content">
            <img
                class="loading-type"
                :src="imgUrl"
            >
            <img
                class="loading-progress"
                :src="progress"
            >
            <p>正在为您打印照片，请耐心等待</p>
        </div>
    </div>
</template>

<script>
import { photoCommitPrintInfo } from '@/service/convenient/identificationPhoto';
import waitCard from '@/assets/images/wait_print.png';
import { mapState, mapMutations } from 'vuex';
import progress from '@/assets/images/progress.gif';
import { cancelPrefix } from '@/utils/tools';
import { printPhotoBase } from '@/service/common';

export default {
    name: 'IdentificationPhotoLoading',
    computed: {
        ...mapState('identificationPhoto', {
            previewPrintPhoto: 'previewPrintPhoto',
            printCount: 'printCount',
            photoPrintInfoId: 'photoPrintInfoId'
        })
    },
    data () {
        return {
            imgUrl: waitCard,
            progress: progress
        };
    },
    mounted () {
        this.printPhoto();
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('identificationPhoto', ['setPrintResult']),
        // 打印照片
        async printPhoto () {
            const result = await printPhotoBase({ printCount: this.printCount, base64: cancelPrefix(this.previewPrintPhoto) });
            if (result && result.code === 1) {
                await this.printInfo();
            } else if (result) {
                this.changeTipPage({ message: result.message, enableBack: true });
            }
        },
        async printInfo () {
            // 保存用户打印照片成功的信息
            const res = await photoCommitPrintInfo({
                photoHistoryId: this.photoPrintInfoId
            });
            if (res && res.code === '0') {
                this.setPrintResult(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    }

};
</script>

<style lang="scss" scoped>
    .confirm-loading {
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 999;
        background-color: rgba(0, 0, 0, 0.7);

        .loading-content {
            width: 400px;
            text-align: center;

            img.loading-type {
                width: 200px;
            }

            img.loading-progress {
                width: 100%;
            }

            > p {
                text-align: center;
                font-size: 26px;
                font-weight: 400;
                color: rgba(255, 255, 255, 1);
                line-height: 36px;

            }

        }
    }
</style>
