import request from '@/utils/request';

/**
 * 获取证件照类型
 * @returns {*}
 */
export const identificationPhotoType = (params) => {
    return request.ajax('/ssm/photo/getPhotoTypeList', 'get', params);
};

/**
 * 获取用户打印照片的信息
 * @returns {*}
 */
export const savePhotoInfo = (params) => {
    return request.ajax('/ssm/photo/savePhoto', 'post', params);
};

/**
 * 获取用户打印照片成功的信息
 * @returns {*}
 */
export const photoCommitPrintInfo = (params) => {
    return request.ajax('/ssm/photo/photoCommitPrint', 'get', params);
};

/**
 * 图片处理
 * @param params
 * @returns {*}
 */
export const getIdentificationPhoto = (params) => {
    return request.ajax('/photoHandler', 'post', params, { isPictureHandle: true });
};

// 支付结果查询
export const checkPayResult = (params) => {
    return request.ajax('/ssm/payOrderMgt/payStatus', 'get', params);
};

/**
 * 图片处理
 * @param params
 * @returns {*}
 * {
				"base64":imageBase64,
				"bgColor":"red"
	}
 */
export const changePhotoColor = (params) => {
    return request.ajax('http://localhost:9099/photoChangeBg', 'post', params, { isLocalHttp: true });
};
