<template>
    <div>
        <div class="kd-page-title">
            请选择证照类别
        </div>
        <div class="wrap-info">
            <div class="category-box">
                <div
                    v-for="(item, index) in categoryList"
                    :key="index"
                    class="category-item"
                    :class="{active:categoryActive===index}"
                    @click="toggle(index, item)"
                >
                    <i v-if="categoryActive===index"><img src="../../../../assets/images/choseIcon.png"></i>
                    <img
                        :src="item.img"
                        alt=""
                    >
                    <div class="desc">
                        <span>{{ item.name }}</span>
                        <span>({{ item.width }}*{{ item.height }}mm)</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :but-disable="!(categoryActive || categoryActive === 0)"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
    name: 'IdentificationPhotoCategory',
    data () {
        return {
            categoryActive: null	// 选中类别
        };
    },
    computed: {
        ...mapState('identificationPhoto', {
            typeList: 'typeList'
        }),
        categoryList: function () {
            const res = this.typeList.find((item) => {
                return item.type === 1;
            });

            return res.childTypes;
        }
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('identificationPhoto', ['setPhotoType']),
        toggle (i, item) {
            this.categoryActive = i;
            this.setPhotoType(item);
        },
        goUp () {
            this.changeStep('up');
            this.setPhotoType(null);
        },
        goNext () {
            // 未选择类别页面不跳转
            if (!(this.categoryActive || this.categoryActive === 0)) {
                return;
            }
            this.changeStep('photoCollection');
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 1080px;
        height: 530px;
        margin: 0 auto;
        text-align: center;

        .category-box {
            margin-top: 53px;

            .category-item {
                display: inline-block;
                margin: 22px 60px;
                color: #fff;
                text-align: center;
                border-radius: 8px;
                cursor: pointer;
                position: relative;

                > i {
                    position: absolute;
                    right: 33px;
                    top: -9px;
                    width: 30px;
                    height: 30px;

                    img {
                        width: 100%;
                        height: 100%;
                    }
                }

                .desc {
                    margin-top: 14px;

                    span:first-child {
                        font-size: 26px;
                        font-weight: 600;
                    }

                    span:last-child {
                        margin-left: 10px;
                        font-size: 18px;
                    }
                }
            }
        }
    }
</style>
