<template>
    <div>
        <div class="kd-page-title">
            请选择证照类型
        </div>
        <div class="wrap-info">
            <el-row>
                <el-col
                    :span="6"
                    v-for="(item, index) in visaList"
                    :key="index"
                    class="detail-item"
                    :class="{active:active === index}"
                    @click.native="toggle(index, item)"
                >
                    <div>
                        <p>{{ item.name }}</p>
                        <p>({{ item.width }}*{{ item.height }}mm)</p>
                    </div>
                </el-col>
            </el-row>
        </div>
        <div class="visa-tip">
            <p>温馨提示：其他国家签证请用中国护照即可</p>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :but-disable="!(active || active === 0)"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
    name: 'IdentificationPhotoChooseVisa',
    data () {
        return {
            active: null
        };
    },
    computed: {
        ...mapState('identificationPhoto', {
            typeList: 'typeList'
        }),
        visaList: function () {
            const res = this.typeList.find((item) => {
                return item.type === 2;
            });

            return res.childTypes;
        }
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('identificationPhoto', ['setPhotoType']),
        toggle (i, item) {
            this.active = i;
            this.setPhotoType(item);
        },
        goUp () {
            this.changeStep('photoType');
            this.setPhotoType(null);
        },
        goNext () {
            // 未选择签证页面不跳转
            if (!(this.active || this.active === 0)) {
                return;
            }
            this.changeStep('down');
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        width: 980px;
        height: 450px;
        margin: 0 auto;

        .detail-item {
            cursor: pointer;
            box-sizing: border-box;
            margin-top: 40px;
            margin-left: 32px;
            width: 208px;
            height: 100px;
            background: linear-gradient(#70A3D8, #3A84BF);
            border: 1PX solid #EDFFFF;/*no*/
            border-radius: 1vh;
            padding: 20px;

            > div {
                text-align: center;

                p {
                    color: #ffffff;
                }

                p:first-child {
                    font-size: 26px;
                }

                p:last-child {
                    margin-top: 10px;
                    font-size: 18px;
                }
            }

            &.active {
                background: linear-gradient(180deg, rgba(251, 149, 39, 1) 0%, rgba(255, 115, 20, 1) 100%);
                border-radius: 8px;
            }
        }
    }

    .visa-tip {
        font-size: 26px;
        color: #fff;
        padding-left: 185px;
    }
</style>
