<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <photo-type v-if="currentStep === steps.findIndex(step=>step.key === 'photoType')"/>
        <photo-category v-if="currentStep === steps.findIndex(step=>step.key === 'photoCategory')"/>
        <choose-visa v-if="currentStep === steps.findIndex(step=>step.key === 'chooseVisa')"/>
        <photo-tip-second v-if="currentStep === steps.findIndex(step=>step.key === 'photoCollection')" :next-function="savePhoto" :size="size"/>
        <collect-complete v-if="currentStep === steps.findIndex(step=>step.key === 'collectComplete')"/>

        <process
            :type="2"
            v-if="currentStep === steps.findIndex(step=>step.key === 'uploadPhoto')"
            :handle-business="savePay"
        />

        <choose-pay
            v-if="currentStep === steps.findIndex(step=>step.key === 'choosePay')"
            :get-method="getMethod"
        />
        <photo-pay v-if="currentStep === steps.findIndex(step=>step.key === 'photoPay')"/>
        <print-loading v-if="currentStep === steps.findIndex(step=>step.key === 'printLoading')"/>
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="data"
            foot-texts="温馨提示：请及时取回您的照片，避免遗忘"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import photoType from './components/type';
import photoCategory from './components/category';
import chooseVisa from './components/choose-visa';
import photoTipSecond from '@/view/pageComponents/photo-tip-second';
import collectComplete from './components/collect-complete';
import photoPay from './components/pay';
import printLoading from './components/loading';
import complete from '@/components/complete';
import { getIdentificationPhoto, identificationPhotoType, savePhotoInfo } from '@/service/convenient/identificationPhoto';
// import { LOCATION_ID } from '@/utils/constant';
import choosePay from '@/components/choose-pay';
import { addPrefix, cancelPrefix } from '@/utils/tools';
import { cloneDeep } from 'lodash';
import { IDENTIFICATION_PHOTO, IDENTIFICATION_PHOTO_NOPAY } from '@/constant/stepsOption';

export default {
    name: 'IdentificationPhotoIndex',
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        process,
        photoType,
        photoCategory,
        chooseVisa,
        collectComplete,
        photoPay,
        printLoading,
        complete,
        photoTipSecond,
        choosePay
    },
    data () {
        return {
            loading: null
        };
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode',
            payMethod: 'payMethod'
        }),
        ...mapState('permanent', {
            lightWebSocket: 'lightWebSocket',
            singleReflexWebSocket: 'singleReflexWebSocket',
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        }),
        ...mapState('identificationPhoto', {
            data: 'result',
            previewPhoto: 'previewPrintPhoto',
            previewPrintPhoto: 'previewPrintPhoto',
            photoType: 'photoType',
            printCount: 'printCount',
            category: 'category',
            visa: 'visa'
        }),
        size () {
            const size = [];
            if (this.photoType.type === 1) {
                size.push(this.category.width);
                size.push(this.category.height);
            } else if (this.photoType.type === 2) {
                size.push(this.visa.width);
                size.push(this.visa.height);
            } else {
                size.push(this.photoType.width);
                size.push(this.photoType.height);
            }
            return size;
        }
    },
    mounted () {
        if (this.CONSTANT.needPayPhoto) {
            this.setSteps(IDENTIFICATION_PHOTO);
        } else {
            this.setSteps(IDENTIFICATION_PHOTO_NOPAY);
        }
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage', 'setPayMethod']),
        ...mapMutations('identificationPhoto', ['setPrintInfoId', 'setTypeList', 'setQriously', 'setOutTradeNo', 'setTotalMoney', 'setPreviewPrintPhoto']),
        async savePhoto (photo) {
            let printerOption = {
                name: this.photoType.name,
                base64: cancelPrefix(photo),
                ...this.photoType,
                layout: JSON.parse(this.photoType.layout)
            };

            printerOption = cloneDeep(printerOption);
            delete printerOption.childTypes;

            const res = await getIdentificationPhoto({
                module: 'PhotoPrinter',
                directive: 'preview',
                obj: printerOption
            });
            if (res && res.code === 1) {
                this.setPreviewPrintPhoto(addPrefix(res.obj));
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async handleBusiness () {
            const res = await identificationPhotoType();
            if (res && res.code === '0') {
                this.setTypeList(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: '未能成功获取数据，请稍后再试！' });
            }
        },
        getMethod (type) {
            this.setPayMethod(type);
            this.savePay(type);
        },
        // 立即支付
        async savePay (type) {
            if (!this.CONSTANT.isOlin) {
                this.loading = this.$loading.full('正在处理中，请稍后......');
            }

            const data = {
                photoImage: this.previewPrintPhoto,
                photoTypeId: this.photoType.id,
                printNum: this.printCount,
                payType: type || 3
            };
            const res = await savePhotoInfo(data);
            if (res && res.code === '0') {
                this.setPrintInfoId(res.result.userBusinessId);
                this.setQriously(res.result.payUrl);
                this.setOutTradeNo(res.result.outTradeNo);
                this.setTotalMoney(res.result.totalPrice);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
            if (this.loading) {
                this.loading.close();
            }
        }
    }
};

</script>
