<template>
    <div class="overprint-photo">
        <el-dialog
            title="加印照片"
            :visible.sync="dialogVisible"
            width="85%"
            :before-close="cancel"
            :close-on-click-modal="false"
        >
            <div class="content">
                <p>请选择加印份数</p>
                <div>
                    <span v-for="(item, index) in typeList" :key="index.id" :class="{active:active===index}" @click="toggle(index, item.count)">{{ item.desc }}</span>
                </div>
            </div>
            <div class="total-money">总金额：￥<span>{{ total }}</span> ({{ price }}*{{ copies }})</div>
            <div slot="footer" class="dialog-footer">
                <kd-button text="取 消" :button-handle="cancel"/>
                <kd-button text="确 定" :button-handle="sure"/>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
    name: 'IdentificationPhotoOverprint',
    props: ['dialogVisible'],
    data () {
        return {
            active: 0,				// 当前选中的
            typeList: [				// 份数数组
                {
                    id: 1,
                    count: 2,
                    desc: '份数×2'
                },
                {
                    id: 2,
                    count: 3,
                    desc: '份数×3'
                },
                {
                    id: 3,
                    count: 5,
                    desc: '份数×5'
                }
            ],
            copies: 2				// 默认选中份数
        };
    },
    computed: {
        ...mapState('identificationPhoto', {
            price: 'price'
        }),
        total () {
            return this.copies * this.price;
        }
    },
    methods: {
        ...mapMutations('identificationPhoto', ['setPrintCount', 'setTotalMoney']),
        // 选择打印的份数
        toggle (i, count) {
            this.active = i;
            this.copies = count;
        },
        // 确定
        sure () {
            this.$emit('sure');
            this.setPrintCount(this.copies);
            this.setTotalMoney(this.total);
        },
        // 取消
        cancel () {
            this.$emit('cancel');
        }
    }
};
</script>

<style lang="scss" scoped>
    .overprint-photo {
        .content {
            p {
                color: #3C4B78;
                font-size: 22.4px;
                font-weight: 500;
                text-align: center;
                margin-top: 30px;
            }

            > div {
                width: 86%;
                margin: 30px auto 0;
                display: flex;
                align-items: center;
                justify-content: space-between;

                span {
                    display: inline-block;
                    width: 200px;
                    height: 80px;
                    line-height: 80px;
                    text-align: center;
                    border: 1PX solid #8E9CB9; /*no*/
                    background: #FAFDFF;
                    border-radius: 8px;
                    color: #8E9CB9;
                    font-size: 22.4px;

                    &.active {
                        border: 1PX solid #3C4B78; /*no*/
                        color: #3C4B78;
                    }
                }
            }
        }

        .total-money {
            color: #3C4B78;
            font-size: 19.2px;
            width: 86%;
            margin: 20px auto 50px;

            span {
                font-size: 28.8px;
                color: #FE5701;
            }
        }

        ::v-deep .el-dialog__header {
            margin: 10px 20px;
            padding: 20px 0 10px;
            color: #606266;
            border-top: 1px solid #D3E4F0;

            .el-dialog__title {
                font-size: 22.4px;
                font-weight: 500;
                color: #3C4B78;
            }

            .el-dialog__headerbtn {
                font-size: 19.2px;

                .el-dialog__close {
                    color: #8A93AE;
                }
            }
        }

        ::v-deep .el-button {
            width: 140px;
            height: 60px;
            border-radius: 8px;
            font-size: 19.2px;
            color: #fff;

            &:first-child {
                background: linear-gradient(180deg, #89BDF2 0%, #4F99E1 100%);
                margin-right: 22px;
            }

            &:last-child {
                background: linear-gradient(180deg, #FB9527 0%, #FF7314 100%);
                border: none;
            }
        }
    }
</style>
