<template>
    <div>
        <div class="kd-page-title">
            选择支付方式
        </div>
        <div class="kd-page-content-wrapper wrap">
            <div
                class="item"
                @click="nextStep(2)"
            >
                <div><img :src="zhifubao"></div>
                <p>支付宝支付</p>
            </div>
            <div
                class="item"
                @click="nextStep(1)"
            >
                <div><img :src="weixin"></div>
                <p>微信支付</p>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import zhifubao from '@/assets/images/zhifubao.png';
import weixin from '@/assets/images/weixin.png';

export default {
    name: 'ChoosePay',
    props: ['getMethod'],
    data () {
        return {
            zhifubao: zhifubao,
            weixin: weixin
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        preStep () {
            this.changeStep('up');
        },
        nextStep (payMethod) {
            if (this.getMethod) {
                this.getMethod(payMethod);
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        justify-content: center;
        align-items: center;

        .item {
            margin: 0 20px;
            position: relative;

            > i {
                position: absolute;
                right: 10px;
                top: 10px;
                width: 30px;
                height: 30px;

                img {
                    width: 100%;
                    height: 100%;
                }
            }

            > div {
                width: 300px;
                height: 300px;
                background: rgba(255, 255, 255, 0.9);
                border-radius: 6px;
                box-sizing: border-box;
                border: 1px solid;
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    width: 50%;
                    height: 50%;
                }
            }

            p {
                font-size: 26px;
                font-weight: 400;
                color: rgba(255, 255, 255, 1);
                line-height: 37px;
                text-align: center;
                margin-top: 30px;
            }

        }
    }
</style>
